<template>
  <div>
    <div class="mb-4" v-if="state == 0"><h3>Jobs</h3></div>
    <div class="mb-4" v-if="state == 1">
      <h3>{{ jobSelected.type }}</h3>
    </div>
    <b-table
      v-if="state != 2"
      class="shadow-sm"
      style="font-size:90%;"
      responsive
      id="mainTable"
      :busy="busy"
      :filter="filterSearch"
      :total-rows="rows"
      :fields="mainTableFields"
      :items="items"
      :per-page="perPage"
      :current-page="currentPage"
      hover
      small
    >
      <template #table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
      <template #cell(action)="row" align="center" style="padding: 0px 0px 0px 0px; margin:0px 0px 0px 0px" >
        <b-button-group class="col-md-5" align="center" size="sm" style="padding: 0px 0px 0px 0px; margin:0px 0px 0px 0px" >
          <b-button
            size="sm"
            variant="outline-info"
            @click="row.toggleDetails"
            class="actionButton ml-4"
            ><i class="fas fa-info"></i>
            {{ row.detailsShowing ? '' : '' }}
          </b-button>
          <b-button
            size="sm"
            variant="outline-primary"
            v-b-toggle:formCollapse
            @click="selectComponent(row.item)"
            class="actionButton ml-1"
            ><i class="far fa-check-square fa-4x"></i
          ></b-button>
        </b-button-group>
      </template>
      <template #row-details="row">
        <b-card>
          <vue-json-pretty
            :showDoubleQuotes="false"
            :showIcon="true"
            :data="row.item"
          />
        </b-card>
      </template>
    </b-table>
    <!-- Inspection Form ------------------------------------------------------------------------->
    <div v-if="state == 2 && jobSelected.type == 'Part'">
      <b-form @submit.prevent="processForm">
        <h4 class="mb-3"><b>Inspection information</b></h4>
        <h5><b>Part :  </b>{{ partSelected.number}}  - {{partSelected.description }}</h5>
        <h5
          v-if="
            partSelected.checkList == false && partSelected.type == 'Single'
          "
          class="mt-3"
        >
       <b> Defect :  </b>{{ defectSelected.description }}
        </h5>
        <b-row class="mt-4 mb-3">
          <b-col cols="4">
            <label for="inspectionDate" class="form-label">Date</label>
            <b-form-datepicker
              v-model="inspectionDate"
            ></b-form-datepicker>
          </b-col>
          <b-col cols="2" class="mr-5">
            <label for="inspectionTime" class="form-label">Time</label>
            <b-row>
              <b-col>
                <b-form-input v-model="timeh" id="timeh"></b-form-input> </b-col
              >:
              <b-col>
                <b-form-input v-model="timem" id="timem"></b-form-input>
              </b-col>
            </b-row>
          </b-col>
          <b-col>
            <label for="refNumber" class="form-label">Ref. No</label>
            <b-form-input v-model="refNumber" id="refNumber"></b-form-input>
          </b-col>
          <b-col>
            <label for="batchNumber" class="form-label">Batch. No</label>
            <b-form-input v-model="batchNumber" id="batchNumber"></b-form-input>
          </b-col>
          <b-col>
            <label for="partId" class="form-label">Part id</label>
            <b-form-input v-model="partId" id="partId"></b-form-input>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col>
            <label for="comment" class="form-label">Comment</label>
            <b-form-input v-model="comment" id="comment"></b-form-input>
          </b-col>
        </b-row>
        <b-row class="mt-3 mb-5">
          <b-col cols="2" style="padding:32px 0px 0px 20px" v-show="defectSelected.photo != 'No photo suport'  && partSelected.checkList == false && partSelected.type == 'Single'" >            
            <b-form-file v-model="files" placeholder="Photo.." multiple></b-form-file>
          </b-col>
          <b-col>
            <label for="inspector" class="form-label">Inspector</label>
            <div class="input-group">
              <b-form-select v-model="inspector">
                <option v-for="insp in optionsInspector" :value="insp" :key="insp.id">{{
                  insp.name
                }}</option>
              </b-form-select>
            </div>
          </b-col>
          <b-col cols="1" style="padding:32px 0px 0px 0px">
            <b-button
              variant="primary"
              class="actionButton"
              @click="addLineInspector()"
              >+</b-button
            >
          </b-col>
          <b-col>
            <b-table
              style="font-size:80%"
              id="inspectors"
              :fields="$constants().inspectorTableFields"
              :items="inspectors"
              small
            >
              <template #cell(action)="row" align="center">
                <b-button-group align="center" size="sm">
                  <b-button
                    size="sm"
                    class="actionButton"
                    variant="outline-danger"
                    @click="deleteLineInspector(row)"
                    ><i class="fas fa-trash-alt"></i
                  ></b-button>
                </b-button-group>
              </template>
            </b-table>
          </b-col>
        </b-row>

        <!-- Single Part Form ------------------------------------------------------------------------->

        <div
          v-if="
            partSelected.checkList == false && partSelected.type == 'Single'
          "       
        >
        <hr class="hr" />
            <b-row class="mt-3">   
              <b-col class="d-grid gap-2 d-md-flex justify-content-md-begin mt-2">
              <b-button
                variant="success"
                class="actionButtonCheck"
                @click="checkDefect('ok',0)"
                pill
                ><i class="far fa-thumbs-up fa-3x"></i>
                </b-button
              >
              </b-col>
              <b-col class="d-grid gap-2 d-md-flex justify-content-md-center mt-2">
              <b-button
                variant="warning"
                type="submit"
                class="actionButton"
                @click="checkDefect('rwk',0)"
                ><i class="fas fa-share fa-2x mr-1"></i>
                <p style="font-size:14px;margin: 20px 0px 0px 0px">
                  REWORK
                </p></b-button
              >
              </b-col>
              <b-col class="d-grid gap-2 d-md-flex justify-content-md-end mt-2">
              <b-button
                variant="danger"
                type="submit"
                class="actionButtonCheck"
                @click="checkDefect('nok',0)"
                pill
                ><i class="far fa-thumbs-up fa-3x fa-rotate-180"></i>
                </b-button
              >
              </b-col>
              
            </b-row>
        </div>
        

        <div
          v-if="partSelected.checkList == true && partSelected.type == 'Single'"
        >
          <div v-for="(defect,index) in partSelected.defects" :key="defect.id">
            <hr class="hr" />
            <b-row>
              <b-col>
                  <i v-if="getDefectInspectionValue(defect) == 'ok'" class="far fa-thumbs-up fa-2x"></i>
                  <i v-if="getDefectInspectionValue(defect) == 'rw'" class="fas fa-share fa-2x"></i>
                  <i v-if="getDefectInspectionValue(defect) == 'nok'" class="far fa-thumbs-up fa-2x fa-rotate-180"></i>
              </b-col>
              <b-col class="mt-1">
                {{ defect.description }}
              </b-col>
              <b-col class="mt-1">
                {{ defect.photo }}
              </b-col>
              <b-col cols="2" style="padding:0px 0px 0px 20px" v-show="defect.photo != 'No photo suport'" >            
                 <b-form-file v-model="files[index]" placeholder="Photo.." multiple></b-form-file>
              </b-col>
              <b-col cols="2" style="padding:0px 0px 0px 20px" v-show="defect.photo == 'No photo suport'" >            
              </b-col>
              <b-col>
                <div class="d-grid gap-2 d-md-flex justify-content-md-center">
                  <b-button 
                    variant="success"
                    class="actionButton"
                    @click="checkDefect('ok',index)"
                    pill
                    ><i class="far fa-thumbs-up fa-2x"></i></b-button
                  >
                  <b-button
                    variant="warning"
                    type="submit"
                    class="actionButton"
                    @click="checkDefect('rwk',index)"
                    ><i class="fas fa-share fa-2x mr-1"></i
                    ></b-button>
                  <b-button
                    variant="danger"
                    type="submit"
                    class="actionButton"
                    @click="checkDefect('nok',index)"
                    pill
                    ><i class="far fa-thumbs-up fa-2x fa-rotate-180"></i></b-button
                  >
                </div>
              </b-col>
            </b-row>
          </div>
          <hr class="hr" />
          <div class="d-grid gap-2 mt-5 d-md-flex justify-content-md-end">
          <b-button
            variant="outline-danger"
            class="actionButton mt-3"
            @click="reset()"
            ><i class="far fa-times-circle mr-1"></i>Cancel</b-button
          >
          <b-button
            variant="outline-success"
            @click="saveInspectionSingle()"
            class="actionButton mt-3"
            ><i class="far fa-check-circle mr-1"></i
            >Save</b-button
          >
        </div>
        </div>

        <!-- Bulk Part Form ------------------------------------------------------------------------->
        <div v-if="partSelected.type == 'Bulk'"  >
          <hr class="hr" />
          <b-card
            class="card-form"
            v-bind:style="[
            checkBulkFieldsValue == false
            ? 'border-width:5px;border-color: white;'
            : 'border-width:5px;border-color: red'
            ]"
          >
          <b-row>
            <b-col class="mt-1">
              <b>Parts to check</b>
            </b-col>
            <b-col>
              <div class="d-grid gap-2 d-md-flex justify-content-md-end">
               
                <b-form-input
                  v-model="qtyPartsToCheck"
                  id="qtyPartsCheck"
                  @change="checkBulkFields"
                  type="number"
                  class="qtyField"
                  
                >
              </b-form-input>
              <b-tooltip target="qtyPartsCheck" triggers="hover">
                  Total of parts
              </b-tooltip>
               
              </div>
            </b-col>
          </b-row>
          <hr class="hr" />
          <b-row>
            <b-col class="mt-2">
              <b>Qty Ok</b>
            </b-col>
            <b-col>
              <div class="d-grid gap-2 d-md-flex justify-content-md-end">
               
                <b-form-input
                  v-model="qtyOkInfo"
                  id="qtyOkInfo"
                  disabled="true"
                  style="background-color:#27A844;color:#FFFFFF"
                  @change="checkBulkFields"
                  type="number"
                  class="qtyField" 
                >
              </b-form-input>
              <b-tooltip target="qtyPartsCheck" triggers="hover">
                  Total of parts
              </b-tooltip>
               
              </div>
            </b-col>
          </b-row>
          <hr class="hr" />
          <b-row class="mt-2">
            <b-col class="mt-1">
              <b>Quantity Rejected (NOK)</b>
            </b-col>
            <b-col>
              <div class="d-grid gap-2 d-md-flex justify-content-md-end">
                
                <b-form-input
                  v-model="qtyRejected"
                  id="qtyRejected"
                  @change="checkBulkFields"
                  type="number"
                  style="background-color:#DC3546;color:#FFFFFF"

                  class="qtyField"
                ></b-form-input>
               
              </div>
              <b-tooltip target="qtyRejected" triggers="hover">
                Total of parts with some defect
              </b-tooltip>
            </b-col>
          </b-row>
          <div v-for="(defect,index) in partSelected.defects" :key="defect.id">
            <hr class="hr" />
            <b-row class="mt-2">
              <b-col class="mt-1">
                {{ defect.description }}
              </b-col>
              <b-col  class="mt-1">
                {{ defect.photo }}
              </b-col>
              <b-col cols="2" style="padding:0px 0px 0px 20px" v-show="defect.photo != 'No photo suport'" >            
                 <b-form-file v-model="filesdef[index]" placeholder="Photo.." multiple></b-form-file>
              </b-col>
              <b-col cols="2" style="padding:0px 0px 0px 20px" v-show="defect.photo == 'No photo suport'" >            
                 
              </b-col>
              <b-col>
                <div class="d-grid gap-2 d-md-flex justify-content-md-end">
                 
                  <b-form-input
                    v-model="qtyDefectBulk[index]"
                    id="qtyDefectBulk"
                    @change="checkBulkFields"
                    type="number"
                    class="qtyField"
                  ></b-form-input>
                
                </div>
              </b-col>
            </b-row>
          </div>

          <hr class="hr" />
          <b-row>
            <b-col class="mt-1">
              <b>Quantity ReWorked (RW)</b>
            </b-col>
            <b-col>
              <div class="d-grid gap-2 d-md-flex justify-content-md-end">      
                <b-form-input
                  v-model="qtyReworked"
                  id="qtyReworked"
                  @change="checkBulkFields"
                  type="number"
                  style="background-color:#FEC107;color:#FFFFFF"
                  class="qtyField"
                ></b-form-input>
              </div>
              <b-tooltip target="qtyReworked" triggers="hover">
                Total of parts with some defect that have been reworked 
              </b-tooltip>
            </b-col>
          </b-row>
          <div v-for="(rework,index) in partSelected.reworks" :key="rework.id">
            <hr class="hr" />
            <b-row class="mt-2">
              <b-col class="mt-1">
                {{ rework.description }}
              </b-col>
              <b-col  class="mt-1">
                {{ rework.photo }}
              </b-col>
              <b-col cols="2" style="padding:0px 0px 0px 20px" v-show="rework.photo != 'No photo suport'" >            
                 <b-form-file v-model="filesrwk[index]" placeholder="Photo.." multiple></b-form-file>
              </b-col>
              <b-col cols="2" style="padding:0px 0px 0px 20px" v-show="rework.photo == 'No photo suport'" >            
                 
              </b-col>
              <b-col>
                <div class="d-grid gap-2 d-md-flex justify-content-md-end">
                 
                  <b-form-input
                    v-model="qtyReworkBulk[index]"
                    id="qtyReworkBulk"
                    @change="checkBulkFields"
                    type="number"
                    class="qtyField"
                  ></b-form-input>
                
                </div>
              </b-col>
            </b-row>
          </div>
          <div class="d-grid gap-2 mt-5 d-md-flex justify-content-md-end">
          <b-button
            variant="outline-danger"
            class="actionButton mt-3"
            @click="reset()"
            ><i class="far fa-times-circle mr-1"></i>Cancel</b-button
          >
          <b-button
            variant="outline-success"
            @click="saveInspectionBulk()"
            class="actionButton mt-3"
            ><i class="far fa-check-circle mr-1"></i
            >Save</b-button
          >
        </div>
      </b-card>
        </div>
      </b-form>
    </div>
    <!-- Product Form ------------------------------------------------------------------------->
    <div v-if="state == 2 && jobSelected.type == 'Product'">
      <b-card style="margin:10px 100px 10px 100px">
        <div class="mb-3">
          <h4><b> Reference: </b>{{productSelected.reference}}</h4>
          <h5><b>Description: </b>{{ productSelected.description }}</h5>
          <b-row class="mt-3">
            <b-col cols=2>Serial number: </b-col><b-col cols="5"> <b-form-input
                      id="serialNumber" v-model="serialNumber"
                    ></b-form-input>      
            </b-col>      
          </b-row> 
        </div>
        <div class="mb-2 mt-5"><b>Points to review</b></div>
        <div
          v-for="(checkPoint,index) in productSelected.checkPoints"
          :key="checkPoint.id"
        >
          <hr class="hr" />
          <b-row>
            <b-col v-if="checkPointStatus[index] != undefined">
                  <i v-if="checkPointStatus[index].status == 'ok'" class="far fa-thumbs-up fa-2x"></i>
                  <i v-if="checkPointStatus[index].status == 'nok'" class="far fa-thumbs-up fa-2x fa-rotate-180"></i>
            </b-col>
            <b-col v-if="checkPointStatus[index] == undefined"></b-col>
            <b-col class="mt-1">{{ checkPoint.description }}</b-col>
            <b-col>
              <div class="d-grid gap-2 d-md-flex justify-content-md-end">
                <b-button variant="success"  @click="checkDefect('ok',index)" class="actionButton" pill>
                  <i class="far fa-thumbs-up fa-2x"></i></b-button>
                <b-button variant="danger"  @click="checkDefect('nok',index)" class="actionButton" pill>
                  <i class=" mr-2 far fa-thumbs-up fa-2x fa-rotate-180"></i>Repair</b-button>
              </div>
            </b-col>
          </b-row>
        </div>
      </b-card>
    </div>
    <!-------------------------------------------------------------------------------------------->
    <b-button
      v-if="state != 0"
      size="sm"
      variant="outline-secondary"
      v-b-toggle:formCollapse
      @click="switchState(state - 1)"
      class="actionButton ml-1 mt-5"
      ><i class="fas fa-step-backward fa-2x"></i
    ></b-button>
    <b-button
      v-if="state != 0 && state != 1"
      size="sm"
      variant="outline-secondary"
      v-b-toggle:formCollapse
      @click="switchState(state - 2)"
      class="actionButton ml-4 mt-5"
      ><i class="fas fa-fast-backward fa-2x"></i
    ></b-button>
  </div>
</template>

<script>
import EreportService from '../services/ereport.service';
import ReportingService from '../services/reporting.service';
import VueJsonPretty from 'vue-json-pretty';
import AuthService from '../services/auth.service';
import 'vue-json-pretty/lib/styles.css';

export default {
  name: 'Job',
  data() {
    return {
      // Inspection fields
      id:'',
      inspectionDate: new Date(),
      inspectionTime:'',
      timeh:'',
      timem:'',
      refNumber:'',
      batchNumber:'',
      partId:'',
      checkValue:'',
      comment:'',
      partInspectedId:'',
      productInspectedId:'',
      inspectors:[],
      qtyPartsToCheck: 0,
      qtyRejected: 0,
      qtyReworked: 0,
      serialNumber:'',
      //----------------------
      state: 0,
      qtyOkInfo:0,
      busy: false,
      perPage: 10,
      currentPage: 1,
      filterSearch: '',
      items: [],
      jobSelected: '',
      partSelected: '',
      productSelected: '',
      defectSelected: '',
      defectIndex: 0,
      inspector:'',
      optionsInspector: [],
      mainTableFields:'',
      itemsInspector:[],
      files:[],
      filesdef:[],
      filesrwk:[],
      checkPointStatus:[],
      defectInspectionList:[],
      reworkInspectionList:[],
      qtyDefectBulk:[],
      qtyReworkBulk:[],
      checkBulkFieldsValue: false
    };
  },
  methods: {
    checkBulkFields()
    {
      this.checkBulkFieldsValue = false;
      console.log("valora");
      if(parseInt(this.qtyRejected)  > parseInt(this.qtyPartsToCheck)){
        //this.showMsgBoxConfirm('Rejected + Reworked cannot be greater than Parts to check','danger','Error','sm');
        //this.qtyRejected = 0;
        //this.qtyReworked = 0; 
        this.checkBulkFieldsValue = true;
      }
      if(parseInt(this.qtyReworked)  > parseInt(this.qtyRejected)){
        //this.showMsgBoxConfirm('Rejected + Reworked cannot be greater than Parts to check','danger','Error','sm');
        //this.qtyRejected = 0;
        //this.qtyReworked = 0; 
        this.checkBulkFieldsValue = true;
      }
      for(var i = 0; i < this.qtyDefectBulk.length; i++)
      {
          if(parseInt(this.qtyDefectBulk[i]) > parseInt(this.qtyRejected)){
            //this.showMsgBoxConfirm('Defect cannot be greater than Rejected','danger','Error','sm');
            //this.qtyDefectBulk[i] = 0;
            this.checkBulkFieldsValue = true;
          }
          
      }
      for(var j = 0; j < this.qtyReworkBulk.length; j++)
      {
          if(parseInt(this.qtyReworkBulk[j]) > parseInt(this.qtyReworked)){
            //this.showMsgBoxConfirm('Rework cannot be greater than Reworked','danger','Error','sm');
            //this.qtyReworkBulk[j] = 0;
            this.checkBulkFieldsValue = true;
          }
      }


      this.qtyOkInfo =  parseInt(this.qtyPartsToCheck) - parseInt(this.qtyRejected);
      console.log(this.checkBulkFieldsValue);
    },
    loadPerson()
    {
      AuthService.getUsers()
            .then(data => {
              this.itemsInspector = data;
              for(let i = 0; i < this.itemsInspector.length; i++)
              {
                this.optionsInspector.push({id: this.itemsInspector[i].id, name:this.itemsInspector[i].username});
              }
            })
            .catch(error => this.catchError(error));
    },
    addLineInspector() {
      this.inspectors.push({name: this.inspector.name});
      console.log("Inspection Date:"+this.inspectionDate);
    },
    deleteLineInspector(row)
    {
      this.inspectors.splice(row.index, 1);
    },
    selectComponent(item) {

      switch (this.state) {
        case 0:
          this.jobSelected = item;
          this.switchState(1);
          break;

        case 1:
          this.resetFormInspection();

          if (this.jobSelected.type == 'Part') {
            this.partSelected = item;
            if (this.partSelected.defects.length > 0) {
              this.switchState(2);
              this.defectSelected = this.partSelected.defects[0];
              this.defectIndex = 0;
              console.log("USER:"+this.$store.state.auth.user.username);
              this.inspectors.push({name: this.$store.state.auth.user.username});
            }
          }
          if (this.jobSelected.type == 'Product') {
            this.productSelected = item;
            this.switchState(2);
          }
          break;
      }
    },
    saveInspectionProduct(checkPoints)
    {
      var dataInspection = 
      {
        "inspectionDate": Date.now(),
        "serialNumber": this.serialNumber,
        "productInspectedId": this.productSelected.id,
        "checkPoints": checkPoints
      }

      EreportService.saveData('productinspection', dataInspection)
        .then(data => {
          this.showMsgBoxConfirm(data.message, 'success', 'Success', 'sm');          
        })
        .catch(error => this.catchError(error));   
    },
    saveInspectionBulk()
    {
        var listDocument = [];
        var defectInspection;
        var reworkInspection;
        var photoControl = 0;

        for(var k = 0; k < this.partSelected.defects.length; k++)
        {
          if(this.partSelected.defects[k].photo == 'Photo required'
          && (this.filesdef[k] == undefined || this.filesdef[k] == '') && this.qtyDefectBulk[k] != undefined && this.qtyDefectBulk[k] != '' && this.qtyDefectBulk[k] > 0)
          {
            this.showMsgBoxConfirm(
              'Photo required',
              'danger',
              'Error',
              'sm'
            );
            photoControl = 1;
          }
          else{
          
            listDocument = [];
            if(this.filesdef[k] != undefined)
            {
              for(let i = 0; i < this.filesdef[k].length; i++)
              {
                listDocument.push({nombre: this.filesdef[k][i].name, ruta:'/inspection/'+this.partSelected.id+'/defect/'+this.partSelected.defects[k].id, tipo:''});
                this.submitFile(this.filesdef[k][i],'/inspection/'+this.partSelected.id+'/defect/'+this.partSelected.defects[k].id);
              }
            }
            this.filesdef[k] = [];
            defectInspection = {
              "idDefect":  this.partSelected.defects[k].id,
              "qtyOk": 0,
              "qtyNok": this.qtyDefectBulk[k],
              "qtyRw": 0,
              "documents": listDocument
            };
            this.defectInspectionList.push(defectInspection);
          }
        }

        for(var j = 0; j < this.partSelected.reworks.length; j++)
        {
          if(this.partSelected.reworks[j].photo == 'Photo required'
          && (this.filesrwk[j] == undefined || this.filesrwk[j] == '') && this.qtyReworkBulk[j] != undefined && this.qtyReworkBulk[j] != '' && this.qtyReworkBulk[j] > 0)
          {
            this.showMsgBoxConfirm(
              'Photo required',
              'danger',
              'Error',
              'sm'
            );
            photoControl = 1;
          }
          else{
            listDocument = [];
            if(this.filesrwk[j] != undefined)
            {
              for(let h = 0; h < this.filesrwk[j].length; h++)
              {
                listDocument.push({nombre: this.filesrwk[j][h].name, ruta:'/inspection/'+this.partSelected.id+'/rework/'+this.partSelected.reworks[j].id, tipo:''});
                this.submitFile(this.filesrwk[j][h],'/inspection/'+this.partSelected.id+'/defect/'+this.partSelected.reworks[j].id);
              }
            }
            this.filesrwk[j] = [];
            reworkInspection = {
              "idRework":  this.partSelected.reworks[j].id,
              "qtyOk": 0,
              "qtyNok": 0,
              "qtyRw": this.qtyReworkBulk[j],
              "documents": listDocument
            };
            this.reworkInspectionList.push(reworkInspection);
          }
        }

        // Save Inspection
        if(photoControl == 0)
        {
          var qtyOk_ = Number(this.qtyPartsToCheck) - Number(this.qtyRejected);
          var dataInspection = 
          {
            "inspectionDate": this.inspectionDate,
            "inspectionTime": this.timeh + ':' + this.timem,
            "refNumber": this.refNumber,
            "batchNumber": this.batchNumber,
            "comment": this.comment,
            "partId": this.partId,
            "partInspectedId": this.partSelected.id,
            "inspectors": this.inspectors,
            "qtyPartsToCheck" : this.qtyPartsToCheck,
            "qtyRejected" : this.qtyRejected,
            "qtyOk" : qtyOk_,
            "qtyReworked" : this.qtyReworked,
            "defects" : this.defectInspectionList,
            "reworks" : this.reworkInspectionList
          }

          EreportService.saveData('partbulkinspection', dataInspection)
            .then(data => {
              this.showMsgBoxConfirm(data.message, 'success', 'Success', 'sm');
                this.resetFormInspection();
            })
            .catch(error => this.catchError(error));
    
          //this.switchState(1);
          this.resetFormInspection(); 
        }
        else
        {
          this.defectInspectionList = [];
          this.reworkInspectionList = [];
        }

    },
    getDefectInspectionValue(defect)
    {
        if(this.defectInspectionList.length > 0)
        {
          var defectInspection = this.defectInspectionList.find(def => def.idDefect == defect.id);
          if(defectInspection != undefined)
          {
            if(defectInspection.qtyOk > 0){return 'ok';}
            if(defectInspection.qtyNok > 0){return 'nok';}
            if(defectInspection.qtyRw > 0){return 'rw';}
          }
        }
    },
    checkDefect(value,index) {
      
      var listDocument = [];

      var qtyOk;
      var qtyNok;
      var qtyRw;

      switch (value) {
        case 'ok':
          qtyOk = 1;qtyRw = 0;qtyNok = 0;
          break;
        case 'rwk':
          qtyOk = 0;qtyRw = 1;qtyNok = 0;
          break;
        case 'nok':
          qtyOk = 0;qtyRw = 0;qtyNok = 1;
          break;
      }

      var defectInspection;

      if(this.partSelected.checkList == false && this.partSelected.type == 'Single')
      { 
          if(this.partSelected.defects[this.defectIndex].photo == 'Photo required'
          && this.files.length == 0)
          {
            this.showMsgBoxConfirm(
              'Photo required',
              'danger',
              'Error',
              'sm'
            );
          }
          else{
            for(let i = 0; i < this.files.length; i++)
            {
              listDocument.push({nombre: this.files[i].name ,ruta:'/inspection/'+this.partSelected.id+'/defect/'+this.partSelected.defects[this.defectIndex].id, tipo:''});
              this.submitFile(this.files[i],'/inspection/'+this.partSelected.id+'/defect/'+this.partSelected.defects[this.defectIndex].id);
            }
            this.files = [];

            defectInspection = {
              "idDefect":  this.partSelected.defects[this.defectIndex].id,
              "qtyOk": qtyOk,
              "qtyNok": qtyNok,
              "qtyRw": qtyRw,
              "documents": listDocument
            };
            this.defectInspectionList.push(defectInspection);

          if (this.partSelected.defects.length > this.defectIndex+1) {
            this.defectIndex = this.defectIndex + 1;
            this.defectSelected = this.partSelected.defects[this.defectIndex];
          } else {
            this.saveInspectionSingle();
          }
        }
      }

      if(this.partSelected.checkList == true && this.partSelected.type == 'Single')
      {
        if(this.partSelected.defects[index].photo == 'Photo required'
          && this.files[index] == undefined)
          {
            this.showMsgBoxConfirm(
              'Photo required',
              'danger',
              'Error',
              'sm'
            );
          }
        else{
          if(this.files[index] != undefined)
          {
            for(let i = 0; i < this.files[index].length; i++)
            {
              listDocument.push({nombre: this.files[index][i].name, ruta:'/inspection/'+this.partSelected.id+'/defect/'+this.partSelected.defects[index].id, tipo:''});
              this.submitFile(this.files[index][i],'/inspection/'+this.partSelected.id+'/defect/'+this.partSelected.defects[index].id);
            }
          }
          this.files[index] = [];
          
          defectInspection = {
            "idDefect":  this.partSelected.defects[index].id,
            "qtyOk": qtyOk,
            "qtyNok": qtyNok,
            "qtyRw": qtyRw,
            "documents": listDocument
          };


          var deleteIndex = this.defectInspectionList.findIndex(el => {
             return el.idDefect == this.partSelected.defects[index].id;
          });
          if(deleteIndex != -1)
          {
            this.defectInspectionList.splice(deleteIndex, 1);
          }

          console.log(this.defectInspectionList);
          this.defectInspectionList.push(defectInspection);
        }
      }

      if(this.jobSelected.type == 'Product')
      {   
        this.checkPointStatus.push({checkPointId:this.productSelected.checkPoints[index].id,status: value});
        if (this.productSelected.checkPoints.length == index+1) {
          this.saveInspectionProduct(this.checkPointStatus);
          this.resetFormInspection();
        }
      }

    },
    saveInspectionSingle()
    {
      var dataInspection1 = 
      {
        "inspectionDate": this.inspectionDate,
        "inspectionTime": this.timeh + ':' + this.timem,
        "refNumber": this.refNumber,
        "batchNumber": this.batchNumber,
        "comment": this.comment,
        "partId": this.partId,
        "partInspectedId": this.partSelected.id,
        "inspectors": this.inspectors,
        "defects": this.defectInspectionList
      }

      EreportService.saveData('partsingleinspection', dataInspection1)
        .then(data => {
          this.showMsgBoxConfirm(data.message, 'success', 'Success', 'sm');
          this.resetFormInspection();

          if (this.partSelected.defects.length > 0) {
              this.defectSelected = this.partSelected.defects[0];
              this.defectIndex = 0;
            }
          //this.switchState(1);
        })
        .catch(error => this.catchError(error));       
    },
    getData(type) {
      this.busy = true;

      switch(type){
        case 'job':
        var sites = '';
          for(var i=0;i < this.$store.state.auth.user.attributes.length;i++)
          {
            if(this.$store.state.auth.user.attributes[i].code == 'site'){
              sites = sites + ',' + this.$store.state.auth.user.attributes[i].value;
            }
          }
          EreportService.getDataByAux(type,sites.substring(1))
            .then(data => {
              this.items = data;
            })
            .catch(error => this.$parent.catchError(error));
            this.busy = false;
          break;

      default:
        EreportService.getData(type)
          .then(data => {
            this.items = data;
          })
          .catch(error => this.$parent.catchError(error));
          this.busy = false;
      }
    },
    switchState(newState) {
      this.state = newState;

      switch (this.state) {
        case 0:
          this.getData('job');
          
          this.mainTableFields = this.$constants().jobInspectionTableFields;
          break;

        case 1:
          if (this.jobSelected.type == 'Part') {
            this.items = this.jobSelected.parts;
            this.mainTableFields = this.$constants().partInspectionTableFields;
          }
          if (this.jobSelected.type == 'Product') {
            this.items = this.jobSelected.products;
            this.mainTableFields = this.$constants().productInspectionTableFields;
          }
          break;

        case 2:
          break;
      }
    },
    submitFile(file, ruta)
    {
      ReportingService.submitFile(file,ruta)
        .then(data => {
          console.log(data.message);
        })
        .catch(error => this.catchError(error));   
    },
    catchError(error) {
      if (error.response.status === 401) {
        this.$store.dispatch('auth/logout');
        this.$router.push('/login');
      } else {
        console.log(error);
        this.showMsgBoxConfirm(
          'A problem occurred. Please contact the administrator',
          'danger',
          'Error',
          'sm'
        );
      }
    },
    showMsgBoxConfirm(msg, vari, title, size) {
      this.$bvModal
        .msgBoxOk(msg, {
          title: title,
          size: size,
          buttonSize: 'sm',
          okVariant: vari,
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
        .then(value => {
          this.msgValue = value;
        });
    },
    reset()
    {
      this.resetFormInspection();
      this.switchState(1);
    },
    resetFormInspection()
    {
      this.id = '';
      this.inspectionDate =  new Date();
      this.inspectionTime = '';
      this.timeh = '';
      this.timem = '';
      this.refNumber = '';
      this.batchNumber = '';
      this.partId = '';
      this.checkValue = '';
      this.comment = '';
      this.partInspectedId = '';
      this.productInspectedId = '';
      this.inspectors = [];
      this.inspector = '';
      this.product = '';
      this.serialNumber = '';
      this.files = [] ;
      this.filesdef = [];
      this.filesrwk = [];
      this.qtyPartsToCheck = 0;
      this.qtyRejected = 0;
      this.qtyReworked = 0;
      this.checkPointStatus = [];
      this.defectInspection = [];
      this.defectInspectionList = [];
      this.reworkInspectionList = [];
      this.qtyDefectBulk = [];
      this.qtyReworkBulk = [];
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    rows() {
      return this.items.length;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
    this.switchState(0);
    this.loadPerson();
  },
  components: { VueJsonPretty }
};
</script>
