import axios from 'axios';
require('dotenv').config();

class ReportingService {

  submitFile(file, ruta) {
    let user = JSON.parse(localStorage.getItem('user'));
    let token = user.accessToken;
    let formData = new FormData();
    formData.append("file", file);
    formData.append("ruta", ruta);

    return axios.post(process.env.VUE_APP_REPORTING_SERVICE+"/api/reporting/file/upload", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
        Authorization: "Bearer " + token,
      },
    });
  }
  getFile(item) {
    let user = JSON.parse(localStorage.getItem('user'));
    let token = user.accessToken;
    var path_splitted = item.nombre.split(".");
    var extension = path_splitted.pop();
    let formData = new FormData();
    formData.append("nombre", item.nombre);
    formData.append("ruta", "/presupuesto/" + this.idAviso);
    formData.append("tipo", extension);

    return axios
      .post(process.env.VUE_APP_REPORTING_SERVICE+"/api/reporting/file/download", formData, {
        responseType: "blob",
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: "Bearer " + token,
        },
      })
      .then(
        (response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", item.nombre);
          document.body.appendChild(link);
          link.click();
          link.parentNode.removeChild(link);
        },
        (error) => {
          this.content = (error.response && error.response.data && error.response.data.message) || error.message || error.toString();
        }
      );
  }
}

export default new ReportingService();